.visited {
  opacity: 0;
  position: absolute;
  float: left;
  width: 200px;
  top: 25%;
  height: 50%;
  text-transform: capitalize;
  color: white;
  padding: 3px;
  border-radius: 3px;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  font-weight: bold; }

div.visited li > img {
  list-style-type: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle; }

div.visited li {
  list-style-type: none; }

#rollover {
  display: inline-block;
  position: relative;
  opacity: 0;
  w3-animate-left: -10px; }

