.pathdash {
  stroke-dasharray: 800;
  animation: draw 2s ease-in;
  animation-duration: 2s; }

@keyframes draw {
  from {
    stroke-dashoffset: 1400; }
  to {
    stroke-dashoffset: 2000; } }

.hoverdash {
  stroke-dasharray: 20;
  animation: draw 4s linear;
  animation-duration: 5s; }

@keyframes draw {
  from {
    stroke-dashoffset: 1400; }
  to {
    stroke-dashoffset: 2000; } }

#deezer-widget {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%; }
